.header {
  text-align: center;
  padding: 50px;
  background: linear-gradient(135deg, #f4a261, #e76f51);
  color: #fff;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.photo {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 3px solid #fff;
  margin-bottom: 20px;
}

h1 {
  font-size: 2.8rem;
  margin-bottom: 10px;
}

h1 .highlight {
  color: #ffcc00;
  font-weight: bold;
}

h2 {
  font-size: 1.8rem;
  margin-bottom: 20px;
  font-style: italic;
}

.description {
  font-size: 1.2rem;
  margin-bottom: 20px;
  font-weight: 300;
}

.social-links a {
  margin: 0 10px;
  color: #ffcc00;
  text-decoration: none;
  font-size: 1.1rem;
  border: 2px solid #fff;
  padding: 8px 15px;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
}

.social-links a:hover {
  background-color: #fff;
  color: #e76f51;
  border-color: #e76f51;
}
